import { Style, Text, Icon, Fill, Stroke} from 'ol/style';
import truckImage from '../../../../assets/images/truckDriveForward.svg';
import selectedTruckImage from '../../../../assets/images/selectedVehicle.svg';

const textStyle = (feature, resolution) => new Text({
		textAlign: 'center',
		font: '400 20px/1 Mukta Mahee',
		text: `ID ${feature.get('vehicleId')}`,
		fill: new Fill({color: 'white'}),
		stroke: new Stroke({color: 'black', width: 3}),
		offsetY: -40,
		// backgroundFill: new Fill({color: 'green'}),
});

const styleFunction = (selected) => (feature, resolution) =>  new Style({
	image: new Icon({
		anchor: [0.5, 0.5],
		src: selected ? selectedTruckImage : truckImage,
		// size: [30,30]
		scale: 0.7,
		rotation:feature.getProperties().rotation,
		rotateWithView: true
	}),
	text: textStyle(feature, resolution)
});


export default {
	selected: styleFunction(true),
	unSelected: styleFunction(false),
};
