import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useStyles from './featureCard.css';
import olMapService from '../../core/services/ol-map.service';
import { select } from '../mining-truck/actions/mining.actions';
import { getSelectedFeature } from '../mining-truck/selectors/mining.selectors';
import closeImage from '../../assets/images/clearBlack.svg';
import { FeatureTypes } from '../../definition/features';
import { phraseSafetyEventFeature } from './pharsers/safetyEventPharser';
import { phraseHazardFeature } from './pharsers/hazardPharser';
import { convertWgs84ToWebMercator } from '../../core/services/projections';
import { convertPositionToText } from '../../core/auxilary/formatting';
import { setMapToStaticState } from '../map/actions/map.actions';
import { TextField } from '@material-ui/core';
import ShareIcon from '@material-ui/icons/Share';
import {
	removeHazard,
	removeSafetyEvent,
	updateHazard,
	updateSafetyEvent,
} from '../../core/services/minecept/mineceptServer';
import { getActiveMenu } from '../../core/selectors/core.selector';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';

const FeatureCard = () => {
	const dispatch = useDispatch();
	const feature = useSelector(getSelectedFeature);
	const activeMenu = useSelector(getActiveMenu);
	const classes = useStyles({ menuOpen: activeMenu !== '' });
	const [description, setDescription] = useState('');

	useEffect(() => {
		if(!feature) return;
		if(feature.remarks){
			setDescription(feature.remarks);
		}else{
			setDescription('');
		}
	}, [feature]);

	useEffect(() => {
		if (!feature) return;
		if (activeMenu === '') return;
		if (feature.featureType === FeatureTypes.safetyEvent && activeMenu === 'safety-event-log') return;
		if (feature.featureType === FeatureTypes.hazard && activeMenu === 'hazard-log') return;
		dispatch(select(null));
	}, [feature, activeMenu]);

	let data, removeMethod, saveMethod, confirmationMessage;

	if (!feature) return (<></>);
	if (feature.featureType === FeatureTypes.safetyEvent) {
		data = phraseSafetyEventFeature(feature);
		removeMethod = removeSafetyEvent;
		saveMethod = updateSafetyEvent;
		confirmationMessage = 'You are about to remove a Safety event do you wish to continue?';
	} else if (feature.featureType === FeatureTypes.hazard) {
		data = phraseHazardFeature(feature);
		removeMethod = removeHazard;
		saveMethod = updateHazard;
		confirmationMessage = 'You are about to remove an obstacle do you wish to continue?';
	} else {
		return (<></>);
	}
	const locationString = convertPositionToText([feature.longitude, feature.latitude]);
	const goToFeature = () => {
		dispatch(setMapToStaticState());
		const position = convertWgs84ToWebMercator([feature.longitude, feature.latitude]);
		olMapService.setCenter(position);
	};
	const handleDescriptionChange = (event) => {
		setDescription(event.target.value);
	};

	const handleRemove = async () => {
		if (!removeMethod) return;

		confirmAlert({
			customUI: ({ onClose }) => (
				<div className={classes.removeBox}>
					<div className={classes.removeTitle}>{confirmationMessage}</div>
					<button onClick={onClose} className={classes.removeCancel}>Cancel</button>
					<button onClick={async () => {
						await removeMethod(feature.id);
						olMapService.clearSelection();
						onClose();
					}}  className={classes.removeConfirm}>Remove!</button>
				</div>
			),
		});

	};

	const handleSave = async () => {
		if (!saveMethod) return;
		await saveMethod(feature.id, description);
	};

	const closeCard = () => dispatch(select(null));
	return (
		<div className={classes.root}>
			<img src={closeImage} className={classes.closeButton} onClick={closeCard} />
			<div className={classes.dataSection}>
				<img src={data.symbol} className={classes.symbol} />
				<span className={classes.label}>{data.label}</span>
			</div>
			<div className={classes.dataSection}>
				{
					data.fields.map((field, key) => (
						<div className={classes.dataField} key={key}>
							<div className={classes.fieldTitle}>{field.title}</div>
							<div className={classes.fieldValue}>{field.value}</div>
						</div>
					))
				}
				<div className={classes.dataField}>
					<div className={classes.fieldTitle}>Geographic Location</div>
					<div className={classes.fieldValue} onClick={goToFeature}>{locationString}</div>
				</div>
			</div>
			<div className={classes.dataSection}>
				<div className={classes.fieldTitle}>Description</div>
				<TextField
					className={classes.description}
					value={description}
					onChange={handleDescriptionChange}
					size="small"
					multiline
					rows={3}
					variant="outlined"
					InputProps={{
						classes: {
							input: classes.descriptionText,
						},
					}}
				/>
			</div>
			<div className={classes.controls}>
				<span className={classes.control} onClick={handleRemove}>Remove</span>
				<span className={classes.control} onClick={handleSave}>Save</span>
				<ShareIcon color="disabled" />
			</div>
		</div>
	);
};

export default FeatureCard;
