import React, { Component } from 'react';
import { connect } from 'react-redux';
import soundList from './soundList.json';

const obstacleMinSeverityForSound = parseInt(process.env.REACT_APP_OBSTACLE_MINIMUM_SEVERITY_FOR_SOUND as string);

let currentSound;

interface IProps {
	zoneAlert: boolean;
	laneAlert: boolean;
	fallingWarning: boolean;
	isGoingReverse: boolean;
	obstacles: any;
	audioStateOn: boolean;
}

const initAudioPlayers = (audioPlayers)=>{
	Object.keys(soundList).forEach((audioType)=>{
		audioPlayers[audioType] =  new Audio(soundList[audioType].src);
		audioPlayers[audioType].loop =  soundList[audioType].loop;
		audioPlayers[audioType].playbackRate = soundList[audioType].speed;
	});
};

const setAudio = (audioElement: HTMLAudioElement, isPlay: boolean) => {
	if (isPlay) {
		audioElement.currentTime = 0;
		audioElement.play();
	} else {
		audioElement.pause();
	}
}

const playAudio = (audioPlayers, audioToPlay: String) =>{
	Object.keys(audioPlayers).forEach((player)=>{
		setAudio(audioPlayers[player], audioToPlay === player);
	});
}

const mapPropsToAlerts = (props) : string => {
	if(props.fallingWarning) return 'falling';
	if(props.isGoingReverse){
		if(props.speedAlert === 4) return 'speed4';
		if(props.angleAlert === 4) return 'angle4';
		if(props.bermAlert === 4) return 'berm4';
		if(props.speedAlert === 3) return 'speed3';
		if(props.angleAlert === 3) return 'angle3';
		if(props.bermAlert === 3) return 'berm3';
		if(props.speedAlert === 2) return 'speed2';
		if(props.angleAlert === 2) return 'angle2';
		if(props.bermAlert === 2) return 'berm2';
	}else if(props.audioStateOn){
		if(props.zoneAlert) return 'zone';
		const maxObstacleSeverity = Math.max.apply(null, props.obstacles.map(o => o.severity));
		if(maxObstacleSeverity >= obstacleMinSeverityForSound){
			if(maxObstacleSeverity === 3) return 'obstacle3';
			if(maxObstacleSeverity === 2) return 'obstacle2';
			if(maxObstacleSeverity === 1) return 'obstacle1';
			return 'obstacle';
		}
		if(props.laneAlert) return 'lane';
	}
	return 'none';
};

class AlertsSound extends Component<IProps> {
	public audioPlayers:any = {};

	constructor(props) {
		super(props);
		initAudioPlayers(this.audioPlayers);
	}

	render() {
		let audioToPlay = mapPropsToAlerts(this.props);
		if(audioToPlay !== currentSound){
			playAudio(this.audioPlayers, audioToPlay);
			currentSound = audioToPlay;
		}
		return (
			<div/>
		);
	}
}

const mapStateToProps = (state) => ({
	zoneAlert: state.adasAlerts.zones.size > 0,
	laneAlert: state.adasAlerts.showLaneAlert,
	fallingWarning: state.mining.isFalling,
	isGoingReverse: state.mining.isGoingReverse,
	obstacles: state.mining.obstacles,
	audioStateOn: state.map.audioStateOn,
	speedAlert: state.mining.alertSwitches.speedAlert ? state.mining.reverseData.SpeedAlert : 0,
	angleAlert: state.mining.alertSwitches.approachAngleAlert ?  state.mining.reverseData.ApproachAngleAlert : 0,
	bermAlert: state.mining.alertSwitches.midAxleHeightAlert ?  state.mining.reverseData.MidAxleHeightAlert : 0,
});

export default connect(mapStateToProps)(AlertsSound);
