import { Style, Icon } from 'ol/style';
import { safetyEventIcons } from './safetyEventIcons';

const styleCache:any = [];

const eventIconStyle = (isSelected,eventType,severity)=> new Style({
	image:new Icon({
		anchor: [0.5, 0.5],
		src: safetyEventIcons[isSelected][eventType][severity],
		// size: [30,30]
		scale: 1,
	})
});

const cacheStyles = () =>{
	let tmpSeverities, tmpEvents;

	for(let isSelected = 0; isSelected < safetyEventIcons.length; isSelected++){
		tmpEvents =[];
		for(let eventType = 0; eventType < safetyEventIcons[0].length; eventType++){
			tmpSeverities = [];
			for(let severity = 0; severity < safetyEventIcons[0][0].length; severity++){
				tmpSeverities.push(eventIconStyle(isSelected,eventType,severity));
			}
			tmpEvents.push(tmpSeverities);
		}
		styleCache.push(tmpEvents);
	}
};

const styleFunction = (selected) => (feature) => {
	const eventType = feature.values_.typeOfEvent || 0;
	const severity = feature.values_.severity || 0;
	try{
		return styleCache[selected?1:0][eventType][severity];
	}catch{
		return styleCache[selected?1:0][0][0];
	}
};

cacheStyles();

export default {
	selected : styleFunction(true),
	unselected : styleFunction(false),
}
