import { Style, Icon } from 'ol/style';
import {obstacleMapIcons } from './obstacleIcons';

const styleCache:any = [];

const obstacleIconStyle = (isSelected,obstacleType,severity)=> new Style({
	image:new Icon({
		anchor: [0.5, 0.5],
		src: obstacleMapIcons[isSelected][obstacleType][severity],
		// size: [30,30]
		scale: 0.7,
	})
});

const cacheStyles = () =>{
	let tmpSeverities, tmpObstacles;

	for(let isSelected = 0; isSelected < obstacleMapIcons.length; isSelected++){
		tmpObstacles =[];
		for(let obstacleType = 0; obstacleType < obstacleMapIcons[0].length; obstacleType++){
			tmpSeverities = [];
			for(let severity = 0; severity < obstacleMapIcons[0][0].length; severity++){
				tmpSeverities.push(obstacleIconStyle(isSelected,obstacleType,severity));
			}
			tmpObstacles.push(tmpSeverities);
		}
		styleCache.push(tmpObstacles);
	}
};

const styleFunction = (selected) => (feature) => {
	const obstacleType = feature.values_.obstacleType || 0;
	const severity = feature.values_.severity || 0;
	try{
		return styleCache[selected?1:0][obstacleType][severity];
	}catch{
		return styleCache[selected?1:0][0][0];
	}
};

cacheStyles();

export default {
	selected : styleFunction(true),
	unselected : styleFunction(false),
}
