import Axios from 'axios';

const MANAGER_ID = process.env.REACT_APP_MANAGER_ID as string;
const HOST = process.env.REACT_APP_SERVER as string;
const PORT = process.env.REACT_APP_SERVER_REST_PORT as string;
const MINECEPT_ROUTE = process.env.REACT_APP_SERVER_MINECEPT_ROUTE as string;
const BASE_ROUTE =`${HOST}:${PORT}/${MINECEPT_ROUTE}/`;

const routes = {
	safetyEvent: `${BASE_ROUTE}safetyevents/`,
	hazard: `${BASE_ROUTE}obstacles/`,
}
export const removeSafetyEvent = async (eventId) =>{
	try{
		await Axios.delete(routes.safetyEvent+eventId,{
			params:{
				manager_id:MANAGER_ID
			}
		});
		return true;
	}catch(e){
		return false;
	}
};

export const updateSafetyEvent = async (eventId, remarks) =>{
	try{
		await Axios.put(routes.safetyEvent+eventId,{
			remarks:remarks
		});
		return true;
	}catch(e){
		return false;
	}
};

export const removeHazard = async (eventId) =>{
	try{
		await Axios.delete(routes.hazard+eventId,{
			params:{
				manager_id:MANAGER_ID
			}
		});
		return true;
	}catch(e){
		return false;
	}
};

export const updateHazard = async (id, remarks) =>{
	try{
		await Axios.put(routes.hazard+id,{
			remarks:remarks
		});
		return true;
	}catch(e){
		return false;
	}
};

export const getAllHazards = async () =>{
	try{
		return await Axios.get(routes.hazard,{
			params: {
				all: true
			}
		});
	}catch(e){
		return false;
	}
};

export const getAllSafetyEvents = async () =>{
	try{
		return await Axios.get(routes.safetyEvent,{
			params: {
				all: true
			}
		});

	}catch(e){
		return false;
	}
};
