import React from 'react';
import { useSelector } from 'react-redux';
import { SnackbarProvider } from 'notistack';
import profileConfig from './profileConfig';
import './App.scss';
import MapControl from './features/map-controls/map-control/MapControl';
import MapContainer from './features/map/map-container/MapContainer';
import Menu from './features/menu/menu/Menu';
import ErrorMessages from './features/error-messages/error-growl/ErrorGrowl';
import StatusBar from './features/sensors/status-indicator/status-bar/StatusBar';
import AdasAlertsDisplay from './features/adas-alerts/adas-alerts-display/AdasAlertsDsiplay';
import AdasAlertsSound from './features/adas-alerts/sounds/AlertsSound';
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import { getIsFalling, getIsGoingReverse } from './features/mining-truck/selectors/mining.selectors';
import FallingWarning from './features/fallingWarning/FallingWarning';
import ReverseAlertBar from './features/mining-truck/reverseAlertBar/ReverseAlertBar';
import MiniMap from './features/mining-truck/miniMap/MinMap';
import MineceptStatusBar from './features/mining-truck/MineceptStatusBar/MineceptStatusBar';
import { initSideEffects } from './core/services/sideEffects';
import useStyles from './app.css';
import SnackBar from './features/snackBar/SnackBar';
import FeatureCard from './features/featureCard/FeatureCard';

const maxNumberOfSnackBars = parseInt(process.env.REACT_APP_MAX_SNAKBARS as string);
const snackBarDuration = parseInt(process.env.REACT_APP_SNAKBAR_DURATION as string) * 1000;

const App: React.FC = () => {
	const classes = useStyles();
	const isFalling = useSelector(getIsFalling);
	const isGoingReverse = useSelector(getIsGoingReverse);
	const showReverseAlertBar = profileConfig.showReverseAlertBar && isGoingReverse;
	const showMiniMap = isGoingReverse ? profileConfig.showMiniMapSafeUnloading : profileConfig.showMiniMapOnGeneralScreen;
	initSideEffects();

	return (
		<SnackbarProvider
			anchorOrigin={{vertical: 'top', horizontal: 'left'}}
			maxSnack={maxNumberOfSnackBars}
			autoHideDuration={snackBarDuration}
			classes={{
				root: classes.snack
			}}
			content={(key, data) => (
				<SnackBar id={key} data={data} />
			)}
		>
			<div className='app'>
				{isFalling && <FallingWarning />}
				{!isGoingReverse && <MapControl />}
				<ErrorMessages />
				{!isGoingReverse && <Menu />}
				<MapContainer />
				{profileConfig.showStatusBar && <StatusBar />}
				<AdasAlertsDisplay />
				<AdasAlertsSound />
				{showReverseAlertBar && <ReverseAlertBar />}
				{showMiniMap && <MiniMap />}
				{profileConfig.showIsConnectedBar && <MineceptStatusBar />}
				<FeatureCard/>
			</div>
		</SnackbarProvider>
	);
};


export default App;
