import { CoreActionTypes } from '../actions/core.actions';
import { startListener } from '../effects/core.effects';

const initialState = {
	activeMenu: '',
	result: ''
};

export default (state = initialState, action) => {
	switch (action.type) {
		// case CoreActionTypes.START_LISTEN_TO_SENSORS:
		// 	startListener();

		case CoreActionTypes.SET_ACTIVE_MENU:
			return {
				...state,
				activeMenu: action.payload
			};
	}
	return state;
}
