import React, { useEffect, useState } from 'react';
import useStyles from './reverseAlertBar.css';
import { useSelector, useDispatch } from 'react-redux';
import AlertBox from './alertBox/AlertBox';
import { getAlertSwitches, getIsBermVisible, getReverseData } from '../selectors/mining.selectors';
import { flipAlertSwitch } from '../actions/mining.actions';
import olMapService from '../../../core/services/ol-map.service';
import Truck from './truck/Truck';
import DiscAlert from './diskAlert/DiscAlert';
import Arrow from './arrow/Arrow';

const truckWidthMeters = parseInt(process.env.REACT_APP_VEHICLE_WIDTH_METERS as string, 10);
const truckImageRatio = parseFloat(process.env.REACT_APP_TRUCK_IMAGE_RATIO as string);
const vehiclePosition = parseFloat(process.env.REACT_APP_REVERSE_AXIS_POSITION as string);
const vehiclePositionOnAxis = 1 - parseFloat(process.env.REACT_APP_REVERSE_VEHICLE_PORTION_ON_AXIS as string);

function ReverseAlertBar() {
	const classes = useStyles();
	const reverseData = useSelector(getReverseData);
	const isBermVisible = useSelector(getIsBermVisible);
	const alertSwitches = useSelector(getAlertSwitches);
	const dispatch = useDispatch();
	const flipSoundSwitch = (selectedAlert) => () => {
		dispatch(flipAlertSwitch(selectedAlert));
	};
	const [truckWidthPX, setTruckWidthPX] = useState(200);
	const [resolution, setResolution] = useState(200);

	useEffect(() => {
		const res = olMapService.getResolution();
		setTruckWidthPX(truckWidthMeters / res);
		setResolution(res);
	});
	// const disc1Position = window.innerHeight*vehiclePosition + truckWidthPX * truckImageRatio * vehiclePositionOnAxis -28;
	const disc1Position = window.innerHeight*vehiclePosition+5;
	const disc3Position = Math.min(window.innerHeight*vehiclePosition + reverseData.DistanceToBermCm/(100*resolution),window.innerHeight -60);
	const disc2Position = (disc1Position + disc3Position)/2;
	const showDisks = isBermVisible && (disc3Position- disc1Position) > 100;
	const isAlert = isBermVisible && (!!reverseData.SpeedAlert || !!reverseData.ApproachAngleAlert || !!reverseData.MidAxleHeightAlert);
	return (
		<div className={classes.root}>
			<div />
			<AlertBox disabled={!isBermVisible} isAlert={!!reverseData.SpeedAlert} symbol={'SD'} label={'SPEED ALERT'}
					  isSoundOn={alertSwitches.speedAlert} soundSwitch={flipSoundSwitch('speedAlert')} />
			<AlertBox disabled={!isBermVisible} isAlert={!!reverseData.ApproachAngleAlert} symbol={'AA'} label={'APPROACH ANGLE'}
					  isSoundOn={alertSwitches.approachAngleAlert}
					  soundSwitch={flipSoundSwitch('approachAngleAlert')} />
			<AlertBox disabled={!isBermVisible} isAlert={!!reverseData.MidAxleHeightAlert} symbol={'MH'} label={'BERM HEIGHT'}
					  isSoundOn={alertSwitches.midAxleHeightAlert}
					  soundSwitch={flipSoundSwitch('midAxleHeightAlert')} />
			<div />
			<Truck truckWidth={truckWidthPX}/>
			{isBermVisible && <DiscAlert isAlert={!!reverseData.SpeedAlert} symbol={'SD'} label1={`${reverseData.SpeedKMH} KMH`} label2={`${reverseData.DistanceToBermCm/100} M`} position={disc1Position}/>}
			{showDisks && <DiscAlert isAlert={!!reverseData.ApproachAngleAlert} symbol={'AA'} label1={`${reverseData.ApproachAngle}`} position={disc2Position}/>}
			{showDisks && <DiscAlert isAlert={!!reverseData.MidAxleHeightAlert} symbol={'MH'} label1={`${reverseData.BermHeightCM/100} M`} position={disc3Position}/>}
			{showDisks && <Arrow isAlert={isAlert} start={disc1Position} end={disc3Position}/>}
		</div>
	);
}

export default ReverseAlertBar;
