import { getSafetyEventLabel, getSafetyEventSymbol } from '../../../core/services/minecept/auxilary';
import moment from 'moment';

export const phraseSafetyEventFeature = (feature) => {
	const timestamp = moment.unix(feature.creation_time);
	return {
		label: getSafetyEventLabel(feature.type_of_event, feature.event_description),
		symbol: getSafetyEventSymbol(feature.type_of_event, feature.severity),
		fields: [
			{ title: 'Vehicle ID', value: feature.created_by },
			{ title: 'Operator', value: feature.operator_id },
			{ title: 'Speed', value: feature.speed },
			{ title: 'Distance', value: feature.distance },
			{ title: 'Angle', value: feature.angle },
			{ title: 'Other Information', value: feature.other },
			{ title: 'Time of Event', value: `${timestamp.format('HH:mm') } ${timestamp.format('DD/MM/YY')}`},
		],
	};
};
