import { makeStyles } from '@material-ui/core/styles';

interface Props{
	disabled:boolean
	isAlert:boolean
}
export default makeStyles({
	root: (props:Props) => ({
		position: 'relative',
		justifySelf: 'center',
		alignSelf: 'center',
		width: 300,
		height: 55,
		borderRadius: 6,
		padding: 7,
		marginTop:5,
		display: 'grid',
		gridTemplateColumns: '35px auto 35px',
		background: props.disabled ?
			'#abadae' :
			props.isAlert ? '#ff6665' : '#91fc9a',
	}),
	symbol:{
		width: 32,
		height: 32,
		borderRadius: 16,
		background: '#28334a',
		color: 'white',
		fontSize: 17,
		textAlign: 'center',
		paddingTop: 3,
		alignSelf: 'center',
		fontFamily: 'Mukta Mahee',

	},
	blinkingWarning:{
		width: 32,
		height: 32,
		animation: 'blink 0.8s',
		alignSelf: 'center',
		animationIterationCount: 'infinite',
	},
	label: props => ({
		color: props.isAlert && !props.disabled ? 'white' : '#0c1334',
		fontFamily: 'Mukta Mahee',
		fontSize: 22,
		fontWeight: props.isAlert ? 500 : 'normal',
		margin: 'auto',
	}),
	speaker: {
		cursor: 'pointer',
		position: 'relative',
		marginTop: 6,
	},
	speakerImage: {
		position: 'absolute',
		left: -3,
		top: -5,
		width: 45,
		height: 45,
	},
});
