import React, { useEffect, useState } from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import useStyles from './SideTable.css';
import { useDispatch, useSelector } from 'react-redux';
import { select } from '../mining-truck/actions/mining.actions';
import closeImage from '../../assets/images/clear.svg';
import moment from 'moment';
import { getSelectedFeatureId } from '../mining-truck/selectors/mining.selectors';
import SortIcon from '@material-ui/icons/Sort';
import ArchiveIcon from '@material-ui/icons/Archive';
import FilterListIcon from '@material-ui/icons/FilterList';
import SortBar from './sortBar/SortBar';
import { SortBy } from '../../definition/Enums';
import FilterBar from './filterBar/FilterBar';

interface Iprops{
	display: boolean
	title: string
	sortTitle:string
	filterTitle: string
	close: ()=>void
	dataSelector: any
	controlsSelector: any
	setSortAction: any
	filterSelector: any,
	setFilters: any
	setArchiveAction: any
	sortOptions: Array<{label: string, value:SortBy}>
	removeNewMessageAlert: any
	typeLabels: Array<string>
}

function SideTable({display, title, close, dataSelector, controlsSelector, setSortAction, filterSelector, setFilters, setArchiveAction, sortOptions, sortTitle, filterTitle, removeNewMessageAlert, typeLabels}:Iprops) {
	const data = useSelector(dataSelector);
	const selectedFeatureId = useSelector(getSelectedFeatureId);
	const dispatch = useDispatch();
	const controls = useSelector(controlsSelector)
	const archive = controls.archive;
	const classes = useStyles({display, archive});
	const [sortMenuOpen, setSortMenuOpen] = useState(false);
	const [filterMenuOpen, setFilterMenuOpen] = useState(false);
	useEffect(()=>{
		dispatch(removeNewMessageAlert);
		setSortMenuOpen(false);
		setFilterMenuOpen(false);
	},[display]);

	const itemSelected = (id: string)=>{
		if(id === selectedFeatureId){
			dispatch(select(null));
		}else{
			dispatch(select(id));
		}
	};

	const onSort = (value) => {
		dispatch(setSortAction(value));
	}

	const toggleArchive = () =>{
		dispatch(setArchiveAction(!archive));
	}

	const toggleSortMenu = ()=>{
		setSortMenuOpen(!sortMenuOpen);
		setFilterMenuOpen(false);
	};

	const toggleFilterMenu = ()=>{
		setFilterMenuOpen(!filterMenuOpen);
		setSortMenuOpen(false);
	};

	return (
		<div className={classes.root}>
			<div className={classes.titleBar}>
				<span>{title}</span>
				<span onClick={close} className={classes.closeButton}><img className={classes.closeSymbol} src={ closeImage }/></span>
			</div>
			<div className={classes.filterBar}>
				<div className={classes.archive} onClick={toggleArchive}>
					<ArchiveIcon fontSize={'small'}/>
					Archived
				</div>
				<div  className={classes.filterBarButtons}>
					<div className={classes.sortButton}>
						<SortIcon onClick={toggleSortMenu}/>
						{
							sortMenuOpen && <SortBar currentValue={controls.sortBy} setSort={onSort} options={sortOptions} close={toggleSortMenu} title={sortTitle}/>
						}
					</div>
					<div className={classes.sortButton}>
						<FilterListIcon onClick={toggleFilterMenu}/>
						{
							filterMenuOpen && <FilterBar close={toggleFilterMenu} title={filterTitle} filterSelector={filterSelector} setFilters={setFilters} typeLabels={typeLabels}/>
						}
					</div>
				</div>
			</div>
			<List className={classes.list}>
				{
					data.map(item=>{
						const timestamp = moment.unix(item.date);
						return <ListItem button key={item.id} className={classes.listItem} onClick={()=>{itemSelected(item.id)}}>
							<img src={item.symbol} className={classes.symbol}/>
							<span className={classes.label}>{item.label}</span>
							<span/>
							<span className={classes.date}>{timestamp.format("HH:mm")} {timestamp.format("DD/MM/YY")}</span>
						</ListItem>
					}

					)
				}
			</List>
		</div>
	);
}

export default SideTable;
