import { getHazardLabel, getHazardSymbol} from '../../../core/services/minecept/auxilary';
import moment from 'moment';

export const phraseHazardFeature = (feature) => {
	const timestamp = moment.unix(feature.creation_time);
	const lastSeenTimestamp = moment.unix(feature.last_seen_time);
	return {
		label: getHazardLabel(feature.obstacle_type, feature.obstacle_description),
		symbol: getHazardSymbol(feature.obstacle_type, feature.severity),
		fields: [
			{ title: 'Vehicle ID', value: feature.created_by },
			{ title: 'Operator', value: feature.operator_id },
			{ title: 'Other Information', value: feature.other },
			{ title: 'Time of Event', value: `${timestamp.format('HH:mm') } ${timestamp.format('DD/MM/YY')}`},
			{ title: 'Last Seen By Vehicle', value: feature.last_seen_by },
			{ title: 'Last Seen By Operator', value: feature.last_seen_by_operator },
			{ title: 'Last Seen At', value: `${lastSeenTimestamp.format('HH:mm') } ${lastSeenTimestamp.format('DD/MM/YY')}`},
		],
	};
};
