const noAlertColor = 'rgba(248,248,248,0.5)'; // '#F8F8F8',
const warningColors = [
	noAlertColor,
	obstacleMinSeverityToAlert <1 ? '#F7BC19' : noAlertColor,
	obstacleMinSeverityToAlert <2 ? '#F59316' : noAlertColor,
	obstacleMinSeverityToAlert <3 ? '#FF6A00' : noAlertColor,
	'#FF0000',
];

