							<path id="Left240" fill={warningColors[sectors[8]]} d="M22.3718814,99.062287 L3.94876847,104.141083 C7.9567653,116.722572 14.4269172,127.872488 24.1597169,137.093311 L37.5868436,123.617819 C30.2431046,116.00639 25.2385526,107.798978 22.3718814,99.062287 Z"/>
