							<path id="Left330" fill={warningColors[sectors[11]]} d="M24.8825884,22.4181532 C34.1094796,13.5972023 45.2079871,6.37439054 59.1811093,3.00451003 L64.1200719,21.3700627 C53.3544886,24.2077199 45.3953834,29.6182856 38.3155376,35.8349342 L24.8825884,22.4181532 Z"/>

