import { safetyEventIcons } from '../layers/styles/safetyEventIcons';
import { obstacleMapIcons } from '../layers/styles/obstacleIcons';
import { safetyEventLabels } from '../../../definition/safetyEvents';
import { convertTimeToText } from '../../auxilary/formatting';
import { convertWgs84ToWebMercator } from '../projections';
import { obstacleLabels } from '../../../definition/obstacles';

export const getSafetyEventSymbol = (type, severity, isSelected = false) => {
	const selected = isSelected ? 1 : 0;
	if (!safetyEventIcons[selected][type]) return safetyEventIcons[0][0][0];
	if (!safetyEventIcons[selected][type][severity]) return safetyEventIcons[0][0][0];
	return safetyEventIcons[0][type][severity];
};

export const getSafetyEventLabel = (type,description) =>{
	if(type === 0 && description)return description;
	if(safetyEventLabels[type]) return safetyEventLabels[type];
	return safetyEventLabels[0];
}


export const phraseSafetyEventsToAlerts = (safetyEvents) => safetyEvents.map(safetyEvent => {
	return {
		label: getSafetyEventLabel(safetyEvent.type_of_event, safetyEvent.event_description),
		symbol: getSafetyEventSymbol(safetyEvent.type_of_event,safetyEvent.severity),
		date: convertTimeToText(safetyEvent.creation_time),
		id: safetyEvent.id,
		position: convertWgs84ToWebMercator([safetyEvent.longitude,safetyEvent.latitude])
	};
});

export const getHazardSymbol = (type, severity, isSelected = false) => {
	const selected = isSelected ? 1 : 0;
	if (!obstacleMapIcons[selected][type]) return obstacleMapIcons[0][0][0];
	if (!obstacleMapIcons[selected][type][severity]) return obstacleMapIcons[0][0][0];
	return obstacleMapIcons[0][type][severity];
};

export const getHazardLabel = (type,description) =>{
	if(type === 0 && description)return description;
	if(obstacleLabels[type]) return obstacleLabels[type];
	return obstacleLabels[0];
}
