import React, { Component } from 'react';
import { connect } from 'react-redux';
import classNames from 'classnames';

import './Menu.scss';
import { setActiveMenu } from '../../../core/actions/core.actions';
import SensorPanel from '../../sensors/sensors-panel/SensorPanel';
import SourceProviderPanel from '../../map/source-provider-panel/SourceProviderPanel';
import AdasLayersProviderPanel from '../../adas-layers/adas-layers-provider-panel/AdasLayersProviderPanel';

import logo from '../../../assets/images/logo.png';
import mapsIcon from '../../../assets/images/Maps.svg';
import eventsIcon from '../../../assets/images/events.svg';
import eventsAlertIcon from '../../../assets/images/eventsNotification.svg';
import hazardsIcon from '../../../assets/images/hazards.svg';
import hazardsAlertIcon from '../../../assets/images/hazardsNotification.svg';
import EditLayerModePanel from '../../adas-layers/edit-layer-mode-panel/EditLayerModePanel';
import { DrawModes } from '../../../definition/draw-modes';
import { AdasLayerStylesEnum } from '../../../definition/adas-layer-styles-enum';
import { editAdasLayer } from '../../adas-layers/actions/adas-layers.actions';
import EditFeatureModePanel from '../../adas-layers/edit-feature-mode-panel/EditFeatureModePanel';
import CreateNewLayerPanel from '../../adas-layers/create-new-layer-panel/CreateNewLayerPanel';
import profileConfig from '../../../profileConfig';
import SideTable from '../../sideTable/SideTable';
import {
	getHazardControls, getHazardFilters,
	getHazardsForTable,
	getSafetyEventsControls, getSafetyEventsFilters,
	getSafetyEventsForTable,
} from '../../mining-truck/selectors/mining.selectors';
import {
	setEventArchive, setEventFilters, setHazardArchive, setHazardFilters,
	setHazardSort,
	setNewHazardFlag,
	setNewSafetyEventFlag,
	setSafetyEventSort,
} from '../../mining-truck/actions/mining.actions';
import { SortBy } from '../../../definition/Enums';
import { safetyEventLabels } from '../../../definition/safetyEvents';
import { obstacleLabels } from '../../../definition/obstacles';

const sortSafetyEventsOptions = [
	{ label: 'Last Added', value: SortBy.time },
	{ label: 'Event Type', value: SortBy.type },
	{ label: 'Event Severity', value: SortBy.severity },
];

const sortHazardsOptions = [
	{ label: 'Last Added', value: SortBy.time },
	{ label: 'Hazard Type', value: SortBy.type },
	{ label: 'Hazard Severity', value: SortBy.severity },
];

class Menu extends Component<any, any> {

	constructor(props) {
		super(props);
	}

	getClassName(activeMenu: string) {
		return classNames({
			'selected': this.props.activeMenu === activeMenu,
			'menu-item': true,
		});
	}

	onSelect(activeMenu: string) {
		if (this.props.activeMenu !== activeMenu) {
			this.props.setActiveMenu(activeMenu);
		} else {
			this.props.setActiveMenu('');
		}
		this.resetEditAdasLayer();
	}

	resetEditAdasLayer() {
		if (this.props.layerId) {
			this.props.editAdasLayer({
				...this.props.adasEditLayer,
				style: AdasLayerStylesEnum.DEFAULT,
				drawMode: DrawModes.NONE,
			});
			this.props.editAdasLayer({ ...this.props.adasEditLayer, id: '' });
		}
	}

	render() {
		return (
			<div className='menu'>

				<div className='panels'>
					<SensorPanel display={this.props.activeMenu === 'sensors'} parentClass="panel-container menu-item"
								 menuClass='menu-item' />
					<SourceProviderPanel display={this.props.activeMenu === 'map-source'} parentClass="panel-container"
										 menuClass='menu-item' />
					<AdasLayersProviderPanel display={this.props.activeMenu === 'adas-layers'}
											 parentClass="panel-container" menuClass='menu-item' />
					<EditLayerModePanel display={this.props.activeMenu === 'edit-layer-mode'}
										parentClass="panel-container menu-item" menuClass='menu-item' />
					<CreateNewLayerPanel display={this.props.activeMenu === 'create-new-layer'}
										 parentClass="panel-container menu-item" menuClass='menu-item' />
					<EditFeatureModePanel display={this.props.activeMenu === 'edit-feature-mode'}
										  parentClass="panel-container menu-item" menuClass='menu-item' />
					<SideTable display={this.props.activeMenu === 'safety-event-log'}
							   title={'Events'} sortTitle={'Sort Events'} filterTitle={'Filter Events'}
							   close={() => {this.props.setActiveMenu('')}}
							   dataSelector={getSafetyEventsForTable}
							   controlsSelector={getSafetyEventsControls}
							   setSortAction={setSafetyEventSort}
							   filterSelector={getSafetyEventsFilters}
							   setFilters ={setEventFilters}
							   typeLabels={safetyEventLabels}
							   setArchiveAction={setEventArchive}
							   sortOptions={sortSafetyEventsOptions}
							   removeNewMessageAlert={setNewSafetyEventFlag(false)} />
					<SideTable display={this.props.activeMenu === 'hazard-log'}
							   title={'Hazards'} sortTitle={'Sort Hazards'} filterTitle={'Filter Hazards'}
							   typeLabels={obstacleLabels}
							   close={() => {this.props.setActiveMenu('')}}
							   dataSelector={getHazardsForTable}
							   controlsSelector={getHazardControls}
							   setSortAction={setHazardSort}
							   filterSelector={getHazardFilters}
							   setFilters ={setHazardFilters}
							   setArchiveAction={setHazardArchive}
							   sortOptions={sortHazardsOptions}
							   removeNewMessageAlert={setNewHazardFlag(false)}/>
				</div>

				<div className='menu-container'>

					<ul>
						<li className={this.getClassName('sensors')} onClick={() => this.onSelect('sensors')}>
							<span className="icon-sensors" />

						</li>
						<li className='border'>
						</li>
						<li className={this.getClassName('map-source')} onClick={() => this.onSelect('map-source')}>
							<img src={mapsIcon} height="42px" width="42px" />
						</li>
						<li className='border'>
						</li>
						<li className={this.getClassName('adas-layers')} onClick={() => this.onSelect('adas-layers')}>
							<span className='icon-layers' />
						</li>
						{
							profileConfig.showSafetEventList && <>
								<li className='border'/>
								<li className={this.getClassName('event-log')}
									onClick={() => this.onSelect('safety-event-log')}>
									<img src={this.props.safetyEventAlert ? eventsAlertIcon : eventsIcon} height="42px"
										 width="42px" />
								</li>
							</>
						}
						{
							profileConfig.showHazardList && <>
								<li className='border'/>
								<li className={this.getClassName('event-log')} onClick={() => this.onSelect('hazard-log')}>
									<img src={this.props.newHazardAlert ? hazardsAlertIcon : hazardsIcon} height="42px"
										 width="42px" />
								</li>
							</>
						}
					</ul>

					<div className='application-name'>
						<img src={logo} />
						<div className='application-version'>{process.env.REACT_APP_APPLICATION_VERSION}</div>
					</div>

				</div>

			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	activeMenu: state.core.activeMenu,
	adasEditLayer: state.adasLayers.adasEditLayer,
	layerId: state.adasLayers.adasEditLayer ? state.adasLayers.adasEditLayer.id : '',
	safetyEventAlert: state.mining.flags.newSafetyEvent,
	newHazardAlert: state.mining.flags.newHazard,
});

const mapDispatchToProps = {
	setActiveMenu,
	editAdasLayer,
};

export default connect(mapStateToProps, mapDispatchToProps)(Menu);
